@charset "UTF-8";
html {
  font-family: 'Open Sans', sans-serif;
  box-sizing: border-box;
  font-size: .625em; }
  @media (max-width: 899px) {
    html {
      font-size: .6em; } }
  @media (max-width: 767px) {
    html {
      font-size: .5em; } }

* {
  margin: 0;
  padding: 0;
  position: relative; }

*, *:before, *:after {
  box-sizing: inherit; }

h1, h2, h3, h4, h5 {
  font-family: 'Roboto Slab', serif; }

h1 {
  font-weight: 700; }

h2 {
  font-weight: 400; }

p {
  font-weight: 300;
  line-height: 1.8em; }

a {
  text-decoration: none;
  color: inherit;
  opacity: 1;
  transition: opacity 0.8s ease-in-out;
  cursor: pointer; }
  a:hover {
    opacity: .5; }

.half {
  width: 50%; }
  @media (max-width: 767px) {
    .half {
      width: 100%; } }

.left {
  float: left; }

.right {
  float: right; }

@media (max-width: 767px) {
  .left,
  .right {
    float: none; } }

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0; }

.clearfix {
  display: inline-block; }

* html .clearfix {
  height: 1%; }

.clearfix {
  display: block; }

.wrap--outer {
  width: 900px;
  margin: 0 auto; }
  @media (max-width: 899px) {
    .wrap--outer {
      width: 100%; } }

.wrap--inner {
  width: 82%;
  max-width: 720px;
  margin: 0 auto;
  padding: 0 0.3rem; }

.sprite {
  display: inline-block;
  max-width: 100%; }

.section--title {
  color: #48910f;
  font-size: 2.2rem;
  line-height: 4rem;
  padding: 2em 0.3rem 0.6em; }

.center {
  text-align: center; }

.fullwidth__withpadding {
  padding-left: 0.3rem;
  padding-right: 0.3rem; }

.btn {
  display: inline-block;
  border-bottom: 1px dashed #e2e2e2;
  margin: 2em 0;
  text-transform: uppercase;
  font-weight: 400;
  font-size: 1.6rem;
  letter-spacing: .3rem;
  transition: .2s ease-in-out;
  padding-bottom: 0.3rem; }
  .btn:hover {
    opacity: 1;
    border-color: #323232; }

#intro {
