  height: 85vh;
  color: #e2e2e2;
  overflow: hidden; }
  #intro .wrap--outer {
    height: 100%;
    width: 100%; }
  #intro .wrap--outer__upperlayer,
  #intro .wrap--outer__underlayer {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    background-attachment: fixed;
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: -1;
    top: 0;
    border-radius: 0 0 0em 0em; }
  #intro .wrap--outer__upperlayer {
    background-image: url("../images/hi_imGG_bg--green.jpg");
    opacity: 0; }
  #intro .wrap--outer__underlayer {
    background-image: url("../images/hi_imGG_bg--normal.jpg"); }
  #intro .wrap--inner {
    height: 100%;
    border-left: 1px dashed #e2e2e2;
    border-right: 1px dashed #e2e2e2; }
  #intro .container,
  #intro .wrap--outer__upperlayer {
    transition: .4s ease-in-out; }
  #intro .container {
    height: 200px;
    top: 0;
    bottom: 0;
    margin: auto;
    position: absolute;
    top: -200%;
    opacity: .4; }
  #intro .left {
    padding-left: 2rem; }
  #intro .right {
    padding-right: 2rem; }
  @media (max-width: 899px) {
    #intro .half {
      padding-right: 1rem;
      padding-left: 0; } }
  @media (max-width: 767px) {
    #intro .half {
      width: 100%;
      padding: 0 1rem; } }
  #intro h1 {
    font-size: 6em; }
  #intro p {
    font-size: 2rem; }
  #intro .svg-goDown {
    margin: 3em 0 1em; }
    @media (max-width: 767px) {
      #intro .svg-goDown {
        margin-left: auto;
        margin-right: auto; } }
  #intro.loaded .container {
    top: 0;
    opacity: 1; }
  #intro.loaded .wrap--outer__upperlayer {
    opacity: 1; }

#journey .wrap--inner {
  border-left: 1px dashed #48910f;
  border-right: 1px dashed #48910f; }

#journey .journey__container {
  font-size: 1.4rem; }

#journey .journey__single {
  background-color: #ffffff;
  border-radius: .2em;
  overflow-y: hidden;
  transition: 0.8s ease-in-out; }
  #journey .journey__single .journey__content {
    opacity: 0;
    transform: translateY(300px);
    transition: 0.8s ease-in-out; }
  #journey .journey__single.showing {
    background-color: #f2f2f2;
    transform: translateY(0px); }
    #journey .journey__single.showing.journey__nextStop {
      background-color: #ffffff; }
    #journey .journey__single.showing .journey__content {
      opacity: 1;
      transform: translateY(0px); }
  #journey .journey__single .wrap--inner {
    padding: 2em; }
    @media (max-width: 767px) {
      #journey .journey__single .wrap--inner {
        padding: 2em 1em; } }
  #journey .journey__single .left {
    width: 80%; }
    #journey .journey__single .left a {
      border-bottom: 1px dashed #48910f; }
    @media (max-width: 767px) {
      #journey .journey__single .left {
        width: 100%; } }

#journey .journey__nextStop {
  font-size: 1.3em; }
  #journey .journey__nextStop p {
    margin-bottom: 1.6em; }

@media (max-width: 767px) {
