#journey .journey__year {
  z-index: 2; }
  #journey .journey__year .svg-journey__year,
  #journey .journey__year .svg-nextStop_block {
    transform: translateY(300px);
    transition: 0.8s ease-in-out; }
  #journey .journey__year.showing .svg-journey__year,
  #journey .journey__year.showing .svg-nextStop_block {
    transform: translateY(0px); }
  #journey .journey__year:first-child .wrap--inner {
    padding-top: 0; }
  #journey .journey__year .wrap--inner {
    padding: 3em 0 1em; }
  #journey .journey__year p {
    color: #e2e2e2;
    display: inline-block;
    text-align: center;
    font-weight: 600;
    font-size: 1rem;
    padding: .2em 0;
    left: -49px; }
    #journey .journey__year p.svg-nextStop_block {
      position: absolute; }
    @media (max-width: 767px) {
      #journey .journey__year p {
        right: 0;
        left: auto;
