  #journey .journey__year h2 {
    display: inline-block;
    color: #48910f;
    vertical-align: middle; }
  #journey .journey__year.journey__year--next {
    text-align: center; }

#journey .journey__meta {
  text-transform: uppercase;
  font-weight: 300;
  font-size: .8em;
  line-height: 1em; }
  #journey .journey__meta span, #journey .journey__meta strong {
    margin-right: 1em; }

#journey .journey__details--content {
  display: none; }
  #journey .journey__details--content ul {
    list-style-position: outside;
    padding: 1em;
    padding-left: 1.5em; }

#journey .journey_details--toggle {
  cursor: pointer;
  padding-top: .4em; }
  #journey .journey_details--toggle .journey_details--btn {
    display: inline-block;
    width: 10px;
    height: 10px;
    margin-right: 0.3rem;
    transition: all .1s ease-in-out;
    transform-origin: 50% 50%;
    transform: rotate(0deg); }
    #journey .journey_details--toggle .journey_details--btn span {
      height: 2px;
      width: 100%;
      display: block;
      background: #323232;
      transform-origin: 50% 50%;
      top: 0;
      bottom: 0;
      margin: auto;
      position: absolute; }
      #journey .journey_details--toggle .journey_details--btn span.line2 {
        transform: rotate(90deg); }
    #journey .journey_details--toggle .journey_details--btn.clicked {
      transform: rotate(45deg); }

