#works{
  .works__all{
    padding:1em $padding_narrow;
    max-width: 1200px;
    margin:0 auto;
    @include phone(){
      text-align: center;
    };
    &>a{
      opacity:0;
      display:inline-block;
      width:(100%/3);
      text-align: center;
      padding:1em $padding_narrow;
      margin:0;
      margin-bottom:2em;
      max-width:400px;
      vertical-align: middle;
      cursor:pointer;
      &.showing{
        opacity:1;
      }
      @include tablet(){
        width:(100%/2);
      }
      @include phone(){
        width:100%;
      }
      .works__img{
        overflow-y:hidden;
        max-width: 100%;
        margin-bottom: 1em;
        height:130px;
        transition: .4s ease-in-out;
        opacity:.8;
      }
      &:hover{
        opacity:1;
        .works__img{
          height: 210px;
          opacity:1;
        }
      }
      img{
        width:100%;
      }
      h3{
        font-family: 'Open Sans', sans-serif;
        font-weight: 300;
        font-size:1.6em;
        margin-bottom:$padding_narrow;
      }
      .works__labels span{
        background-color:$grey;
        margin:$padding_narrow;
        padding:.2em .6em;
        display:inline-block;
      }
    }
  }
}
